import { ItemType } from '../types/_item';
import FileServices from '../services/_fileServices';
export default class MyFile {
    constructor(id, name, fileExtension, modified, modifiedBy, createdTime = Date.now().toString()) {
        this.modifiedBy = Date.now().toString();
        this.itemType = ItemType.File;
        this.createdTime = Date.now().toString();
        this.id = id;
        this.name = name;
        this.fileExtension = fileExtension;
        this.modified = modified;
        this.modifiedBy = modifiedBy;
        this.createdTime = createdTime;
    }
}
export class FileHelper {
    constructor() {
        this.fileService = new FileServices();
        // upload file
        this.uploadFile = async (fileForCreation, cb) => {
            try {
                const fileData = await this.fileService.uploadFile(fileForCreation);
                cb({ data: fileData });
            }
            catch (error) {
                return cb({ error });
            }
        };
        this.deleteFile = async (fileId, cb) => {
            try {
                await this.fileService.deleteFile(fileId);
                cb({ data: fileId });
            }
            catch (error) {
                return cb({ error });
            }
        };
        this.updateFile = async (fileUpdate, cb) => {
            try {
                const fileData = await this.fileService.updateFile(fileUpdate);
                cb({ data: fileData });
            }
            catch (error) {
                return cb({ error });
            }
        };
        this.downloadFile = async (fileId, cb) => {
            try {
                const response = await this.fileService.downloadFile(fileId);
                cb({ data: response.data, contentType: response.headers['content-type'] });
            }
            catch (error) {
                return cb({ error });
            }
        };
    }
}
