import axios from '../utilities/_axios';
class FileServices {
    constructor() {
        this.uploadFile = async (fileForCreation) => {
            const formData = new FormData();
            Object.keys(fileForCreation).forEach(key => {
                formData.append(key, fileForCreation[key]);
            });
            const response = await axios.post('/api/files/upload', formData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            });
            return response.data;
        };
        this.deleteFile = async (fileId) => {
            const response = await axios.delete(`/api/files/${fileId}`);
            return response.data;
        };
        this.updateFile = async (fileUpdate) => {
            const response = await axios.put(`/api/files`, fileUpdate);
            return response.data;
        };
        this.downloadFile = async (fileId) => {
            const response = await axios.get(`/api/files/${fileId}/download`, {
                responseType: 'arraybuffer'
            });
            return response;
        };
    }
}
export default FileServices;
