import $ from 'jquery';
import { ItemType } from '../types/_item';
const ready = (fn) => {
    if (document.readyState !== 'loading') {
        fn();
    }
    else {
        document.addEventListener('DOMContentLoaded', fn);
    }
};
export const clearInput = () => {
    $('#name').val('');
    $('#modified').val('');
    $('#modifiedBy').val('');
    $('#modal-title').removeAttr('data-id');
};
export const mapItems = (items) => {
    const fileMapper = {};
    const folderMapper = {};
    items.forEach(item => {
        if (item.itemType === ItemType.File) {
            fileMapper[item.id] = item;
        }
        else if (item.itemType === ItemType.Folder) {
            folderMapper[item.id] = item;
        }
    });
    return { fileMapper, folderMapper };
};
export default ready;
